import Search from './components/search'
import Ticket from './components/ticket'
import Classify from './components/classify'
import Content from './components/content'
import Article from './components/article'
import { getData,getGoods } from '../../common/cloudFun.js'		//操作数据库组建
import {mapState} from 'vuex'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"		//导入上拉加载组件

export default {
	name:'index',
	components:{
		Search,
		Ticket,
		Classify,
		Content,
		Article,
		uniLoadMore
	},
	data() {
		return {
			isFixed: false,
			rect:'',
			menutop:'',
			banner:[],
			tab:[],
			list:[],
			loadinglist:false,      //loading 的切换状态
			pageid:0,               //上啦加载值
			nav:'',                 //tab切换拿到的集合
			uniload:'loading',      //上拉加载状态
			loadmore:false,         //隐藏上拉加载
			nonedata:false,         //tab切换没有数据的提示
			homeload:true,          //进入首页loading加载
		}
	},
	created() {
		//请求数据
		let banner = "banner"
		let tab = "tab"
		let lising = "recommend"
		//基于promise.all 批量接受多个接口，同时得到多个数据
		Promise.all([getData(banner),getData(tab),getGoods(lising,this.pageid)])
		.then(res=>{
			console.log(res)
			this.banner = res[0].result.data		//轮播数据
			this.tab = res[1].result.data			//tab数据
			this.list = res[2].result.data			//推荐数据
			//所有数据请求成功消除loading
			this.homeload = false
		})
		.catch(err=>{
			console.log(err)
		})
	},
	methods: {
		//上拉加载
		pullon(){
			let listing = this.nav
			getGoods(listing,this.pageid)
			.then((res)=>{
				console.log(res)
				//如果没数据
				if(res.result.data.length == 0){
					this.uniload = 'noMore'
				}else{
					//合并数据
					this.list = [...this.list,...res.data]
				}
			})
			.catch((err)=>{
				console.log(err)
			})
		}
	},
	//监听页面滚动
	onPageScroll(e){
		this.rect = e.scrollTop
	},
	onLoad() {
		const query = uni.createSelectorQuery()
		query.select("#boxFixed").boundingClientRect()
		query.exec((res)=>{
			// console.log(res)
			this.menutop = res[0].top
		})
	},
	// 上拉加载
	onReachBottom(){
		// 上拉加载显示上拉加载组件
		this.loadmore = true
		this.uniload = 'loading'
		console.log('触底')
		this.pageid++
		console.log(this.pageid)
		this.pullon()
	},
	//计算属性:时刻监听数据变化，数据发生变化，计算属性重新执行
	computed:{
		//取出vuex数据仓库的数据
		...mapState(['alist','load','navmin','nonemin']),
		//取到tab切换的数据
		count(){
			this.list = this.alist.listing
		},
		countload(){
			this.loadinglist = this.load.loading
		},
		//以对象传值
		navdata(){
			this.loadinglist = this.navmin.loading
			this.nav = this.navmin.naving
			this.pageid = this.navmin.pageid
			this.loadmore = this.navmin.uniload
			this.nonedata = this.navmin.nonedata
		},
		//滑动组件置顶
		namepage(){
			if(this.rect>this.menutop){
				this.isFixed = true
			}else{
				this.isFixed = false
			}
		},
		//tab切换没有数据的情况
		noneted(){
			this.nonedata = this.nonemin.nonedata
		}
	},
}
